.section_title {
    margin-top: 6rem;
    margin-bottom: 3rem;
    /* padding-bottom: 3rem; */
    font-size: var(--h1-font-size);
}

.about_img {
    width: 11rem;
    height: 11rem;
    border-radius: 100%;
}

.about_container {
    grid-template-columns: 2fr 9fr;
    column-gap: 1.875rem;
}

.about_data {
    padding: 1.875rem;
    background-color: var(--container-color);
    box-shadow: (var(--shadow));
    border-radius: var(--border-radius);
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1.875rem;
    align-items: flex-start;
    position: relative;
}

.about_data::before {
    content: '';
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-right: 10px solid var(--container-color);
    position: absolute;
    left: -0.93rem;
    top: 20%;
}

.about_description {
    margin-bottom: 1rem;
    text-align: justify;
}

.about_skills {
    row-gap: 1.25rem;
}

.skills_titles {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
}

.skills_names {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
}

.skills_number {
    line-height: 1.2;
}

.skills_bar,
.skills_percentage {
    height: 7px;
    border-radius: 0.25rem;
}

.skills_bar{
    background-color: #f1f1f1;
}

.skills_percentage {
    display: block;
    background-color: var(--first-color);
}

.development {
    width: 85%;
    background-color: rgb(255, 163, 179);
}

.dsa {
    width: 70%;
    background-color: rgb(254, 254, 124);
}

.problem {
    width: 80%;
    background-color: rgb(89, 89, 210);
}
.photo {
    width: 90%;
}

.about_boxes {
    grid-template-columns: repeat(3,1fr);
    column-gap: 2rem;
    margin-top: 5rem;
    margin-bottom: 4rem;
}

.about_box{
    display: flex;    
    column-gap: 1.5rem;
}

.about_icon {
    font-size: var(--h1-font-size);
    color: #dedeea;
}

.about_title {
    font-size: 1.875rem;

}


@media screen and (max-width: 1024px) {
    .about_container{
      grid-template-columns: 720px;
      justify-content: center;
      row-gap: 1.875rem;
    }

    .about_data::before {
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 10x solid#fff;
        left: 49%;
        top: -20px;
    }

    .about_img {
        justify-self: center;
    }

    .about_box {
        flex-direction: column;
        row-gap: .5rem;
        text-align: center;
    }

}

@media screen and (max-width: 768px) {
    .about_container {
        grid-template-columns: 350px;
    }    
    
    .about_data::before {
        left: 47%;
    }

    .about_data {
        grid-template-columns: 1fr;
        row-gap: 1.875rem ;
    }

    .about_boxes {
        grid-template-columns: repeat(2, 150px);
        row-gap: 1.5rem;
        justify-content: center;
    }
}

@media screen and (max-width: 576px) { 
    .about_container {
        grid-template-columns: 320px;
    }
}

@media screen and (max-width: 350px) { 
    .about_container {
        grid-template-columns: 1fr;
    }
    
    .about_boxes {
        grid-template-columns: 1fr;
    }
}

